import React, { PropTypes } from 'react';
import TitleBar from '../common/TitleBar';
import Card from '../common/Card';
import zhoubian from '../../images/bigTop_ninjia.png';
import Button from '../common/Button';

class HotZhouBian extends React.Component {
  render () {
    let styles={
      root:{
        marginTop:"0.13rem",
        marginBottom:"0.13rem",
        paddingBottom:"0.13rem",
        background:"#fff"
      }
    }
    return(
      <div style={styles.root}>
          <TitleBar title=""/>
          <Card imgSrc={zhoubian}
                href="http://morethan.cc/wap/good.php?id=444"
                title=""
                miaoshu="         "/>
          <Button href="http://morethan.cc/wap/good.php?id=444" title=" 39"
            style={{marginTop:"-0.46rem",marginLeft:"40%"}}/>
      </div>
    )
  }
}

export default HotZhouBian;



